body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.body
{
  width:100%;
  height:calc(100% - 60px);
  position: absolute;
  top:60px;
}
.body > *
{
  height: 100%;
}
.header
{
  position:fixed;
  top:0;
  width:100%;
  z-index: 10;
}


.my-header
{
  box-sizing: border-box;
  width:100%;
  height:60px;
  background:#5a66b9;
box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.4);
display: flex;
flex-direction: row;
align-items: center; 
padding:0px 20px;
}
.title
{
font-size: 34px;
letter-spacing: 10px;
color:white
}

.base-add-new-div
{
  display:flex;
  flex-direction: row;
  height: 100%;
}

.base-left-div
{
flex-basis:25%;
background:#e8ebff;
display: flex;
flex-direction: column;
padding:10px;
height: 100%;
box-sizing: border-box;
}
.base-left-div:first-child
{
  font-size: 40px;
  font-weight: bold;
  color: #00000059;
}
.base-right-div
{
flex-basis:75%;
height: 100%;
}
.div1
{
  width:100%;
  height:100%;
  display:flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.div1:hover
{
cursor: pointer;
}
.center-div
{
  border :10px dashed lightgray;
  border-radius:10px;
  width:200px;
  height:200px; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.center-div > div:first-child{
  font-size: 80px;
  font-weight: bold;
  color:lightgray;
}
.center-div > div:nth-child(2){
  font-size: 30px;
  font-weight: normal;
  color:lightgray;
  text-align: center;
  margin-top:-10px;
}




.bg {
  -webkit-animation:slide 3s ease-in-out infinite alternate;
          animation:slide 3s ease-in-out infinite alternate;
  background-image: linear-gradient(-60deg, #5a66b9 50%, #525ac1 50%);
  bottom:0;
  left:-50%;
  opacity:.5;
  position:fixed;
  right:-50%;
  top:0;
  z-index:-1;
}

.bg2 {
  animation-direction:alternate-reverse;
  -webkit-animation-duration:4s;
          animation-duration:4s;
}

.bg3 {
  -webkit-animation-duration:5s;
          animation-duration:5s;
}

@-webkit-keyframes slide {
  0% {
    -webkit-transform:translateX(-25%);
            transform:translateX(-25%);
  }
  100% {
    -webkit-transform:translateX(25%);
            transform:translateX(25%);
  }
}

@keyframes slide {
  0% {
    -webkit-transform:translateX(-25%);
            transform:translateX(-25%);
  }
  100% {
    -webkit-transform:translateX(25%);
            transform:translateX(25%);
  }
}

