.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 10s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.body
{
  width:100%;
  height:calc(100% - 60px);
  position: absolute;
  top:60px;
}
.body > *
{
  height: 100%;
}
.header
{
  position:fixed;
  top:0;
  width:100%;
  z-index: 10;
}

